* {
    box-sizing: border-box;
}
body{
    margin: 0;
}

.nav{
    background-color: gold;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;

}
.nav li.active{
    background-color: black;
}
.nav li:hover{
    background-color: white;
}
