.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}

#nav-items {
  display: flex
}     

.hero-image {
  width: 100%; /* Adjust the width as needed */
  border-radius: 40px; /* Add rounded edges */
}

.emergency-button-container {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 1000;
}

.emergency-button {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}

.emergency-button img {
  width: 50px; /* Adjust the size of the image as needed */
  height: 50px; /* Adjust the size of the image as needed */
}

#emergencyButton:hover {
  cursor: help;
}